import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import '../../App.css';


const useStyles = makeStyles({
 
  title: {
    fontSize: 15,
    color: '#404B5A',
    margin: 0
  },
  action: {
   padding: '0 5px',
   marginTop: '10px',
   height: 50
  
  },
  input_text: {   
    padding: 10,   
    display: 'block', 
    width: '95%', 
    fontSize: '15px',
    lineHeight: '18px',
    backgroundColor: '#fff',    
    margin: '10px 0',
    border: 'none'
  },  
  input_rewrite: {   
    padding: 10,
    display: 'block', 
    width: '95%',
    color: "#000", 
    fontSize: 'medium',
    backgroundColor: '#fff',    
    margin: '10px 0',
    border: 'none'
  },  
  text: {     
    padding: 10,
    wordBreak: 'break-all',
    fontSize: 'medium',
    backgroundColor: '#fff',    
    margin: '10px 0 0',
    boxShadow: '0px 1px 1px rgba(0, 0, 0, 0.25)',
  }
});


const InWork = ({name,prev,text, add_text}) => {
  const [open, open_dialog] = React.useState(false);
  const [card_name, set_card_name] = React.useState();
  const [index, set_index] = React.useState();
  

  const [inWork, add_to_inWork] = React.useState(prev);
  const [card, add_card] = React.useState(true);
  const [text_inWork, add_text_inWork] = React.useState();
  const [text_rev, add_text_rev] = React.useState();
  const [backet, add_to_backet] = React.useState([]);
  
  



  

    const saveTocard = (a) => {   
        var s = inWork
        .filter((i,index)=>index === a ? i.two = text_rev : i.two)
        .filter((i,index)=>index === a ? i.one = card_name : i.one);       
        return (
          add_to_inWork(s)
        )    
    };

    const addTextInRev =(value)=>{
        add_text_rev(value)
    };

    const handleClose =() => {
        open_dialog(false)
    };

  const write_to_card = (a,b,c) => {
    return (
      open_dialog(true),     
      set_card_name(a),
      add_text_rev(b),
      set_index(c)
    )  
  };

    const add_new_card = () => {
        var new_text = [{"one": text_inWork}];
        return (
        text_inWork ?      
        (add_to_inWork(inWork.concat(new_text)),
        add_card(true))
        :
        add_card(true)
        );
    };

    const del_card = (a) => {
        let s = inWork.filter((i,index)=>index===a ? "":i)
        return (
            add_to_inWork(s)
        )
    }; 

    const classes = useStyles();

    const to_extract = () => {
        let l = backet.length;
        let s = backet.filter((i,index)=>index+1===l ? "":i);
        return (
            add_to_inWork(inWork.concat(backet[l-1])),
            add_to_backet(s)
        )
    };
   
    const dragStart =(e,a,b)=> {
        let s = [{"one":a.one,"two":a.two}];
        return (
            add_text(text.concat(s)),
            setTimeout(()=>del_card(b),1000)
        )    
    };
    const onDrop = (e) => {       
        return ( 
            inWork.filter(i=>i.one===text[0].one).length>0 ? add_to_backet(backet.concat(text)):"",            
            add_to_inWork(inWork.concat(text)),
            add_text([])           
        )       
    };
    const dragOver = (e) => {
        e.preventDefault();
    };
    return (
    <div className="root"  onDrop={(e)=>onDrop(e)} onDragOver={(e)=>dragOver(e)} >       
      <p className={classes.title}>{name}</p>
      {inWork.map((i,index)=>        
        <p 
            onDragStart={(e)=>dragStart(e,i,index)}
            title = "edit,  movie or delete"
            draggable = "true"
            className={classes.text} 
            onClick={()=>write_to_card(i.one,i.two,index)}
            >{i.one}</p>
        
       )}  
    
     
      {card ?        
        <Button  className={classes.action} onClick={()=>add_card(false)}>
          <span className="plus">+</span>
          –î–æ–±–∞–≤–∏—Ç—å –µ—â–µ –æ–¥–Ω—É –∫–∞—Ä—Ç–æ—á–∫—É
        </Button>        
        : 
        <>
          <textarea placeholder="–í–≤–µ–¥–∏—Ç–µ –∑–∞–≥–æ–ª–æ–≤–æ–∫" wrap="soft" rows="1" cols="30" className={classes.input_text} onChange={(e)=>add_text_inWork(e.target.value)} />
         
          <Button size="medium" onClick={()=>add_new_card()} variant="contained" color="primary">
            –î–æ–±–∞–≤–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É        
          </Button>
          <Button size="medium" onClick={()=>add_card(true)} style={{margin: 10}}>
            X –û—Ç–º–µ–Ω–∞      
          </Button>
        </>
      }
        <span 
            role="img" 
            aria-label="wastebasket" 
            className="backet"
            onClick = {()=>backet.length > 0 ? to_extract() : ""}
            style={backet.length > 0 ? {opacity: 1} : {opacity:'0.5'}} 
            title={backet.length > 0 ? `${backet.length} card` : 'basket empty'}
        
        >
           üóëÔ∏è
        </span>
        <Dialog
            open={open}
            maxWidth="sm"       
            fullWidth="true"
            onClose={handleClose}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle id="alert-dialog-title"  style={{backgroundColor:"#d4d4d4"}}>
            <p style={{margin: 0,color: '#000',fontSize: 24}}>
                
                <Button onClick={handleClose} color="primary" style={{float:"right",marginTop: '-15px',marginRight: '-20px'}}>
                X
                </Button>
                <input className={classes.input_text} value={card_name} onChange={(e)=>set_card_name(e.target.value)} />
            </p>
            </DialogTitle>
            <DialogContent  style={{backgroundColor:"#d4d4d4",padding:"0 24px"}}>         
            <textarea
                value={text_rev} rows="5" cols="20"
                style={{fontSize: 16,color: '#000'}}
                className={classes.input_rewrite}
                onChange={(e)=>addTextInRev(e.target.value)} 
            />
            
            </DialogContent>
            <DialogTitle  style={{backgroundColor:"#d4d4d4",paddingTop: 0}}>
            <p>
                <Button
                    onClick={()=>saveTocard(index)}
                    color="primary"
                    variant="contained" 
                    style={{float:"left"}}
                >–°–æ—Ö—Ä–∞–Ω–∏—Ç—å
                </Button>
            </p>          
            </DialogTitle>
        </Dialog>   
    </div>

    )
};

export default InWork;